<template>
  <b-container class="bv-example-row mt-3">
    <b-row>
      <b-col>
        <b-alert show><h3></h3></b-alert>
      </b-col>
    </b-row>
    <b-row>
      <b-col><b-alert show variant="danger">...</b-alert></b-col>
    </b-row>
  </b-container>
</template>

<script>
import http from "@/util/http-common";

export default {
  name: "BoardDelete",
  created() {
    http.delete(`/board/${this.$route.params.articleno}`).then((response) => {
      let msg = "   .";
      console.log(response);
      if (response.status === 200) {
        msg = " .";
      }
      alert(msg);
      //  route /list .
      this.$router.push({ name: "BoardList" });
    });
  },
};
</script>

